html,
body {
  height: 100%;
}
body {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100%;
  background-color: #fff;
  overflow: auto;
}

footer {
  margin-top: auto;
}

.Layout {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100%;
}
.Layout-main {
  margin-left: 25px;
  margin-bottom: 25px;
}


.Footer {
  background-color: #20232a;
  color: #ffffff;
  padding-top: 20px;
  padding-bottom: 30px;
}
.Footer-row {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.Footer-col {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.Footer-link {
  color: white;
  line-height: 2;
}
.Footer-link:hover {
  color: white;
}

.Blog-posts-by-year {
  margin-top: 12px;
}
.Blog-list {
  list-style: none;
}
.Blog-post {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding-top: 40px;
  padding-bottom: 40px;
}
.Blog-list li {
  line-height: 1.75em;
}
.Blog-list .Blog-date {
  font-style: italic;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.5);
  margin-right: 7px;
}
.Blog-list .Blog-title {
  font-size: 24px;
  font-weight: 700;
  color: #20232a;
}

.PostLayout {
  padding: 10px;
  margin-bottom: 30px;
}
.PostLayout p {
  font-size: 16px;
}

.Image {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
}

.Header {
  font-size: 60px;
  font-weight: 700;
}


.NotFound {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 50px;
  padding-bottom: 50px;
}
.NotFound-header {
  font-weight: 700;
  font-size: 6rem;
  color: #20232a;
}
.NotFound-sub-header {
  font-size: 2rem;
  color: #aaa;
}

